:root {
    --c0: #fff;
    --c1: #EB74F9;
    --c2: #270A4A;
    --c3: #204C7D;
    --c4: #580B65;
    --c5: rgba(0,0,0,0);
}
html { margin: 0; padding: 0; color: var(--c0); }
body { 
    margin: 0; padding: 0; width: 100vw; height: 100vh;
    /* background-image: url("bg-hayny.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; */
    background-color: #333;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.backgroundLayer {
    width: 100vw; height: 100vh; margin: 0; padding: 0;
    position: absolute; top: 0; left: 0; 
}
#bg0 {
    background-image: linear-gradient(var(--c3),var(--c4));
}
#bg1 {
    width: 200vw;
    height: 200vh;
    top: -50vh;
    left: -50vw;
    background-image: repeating-linear-gradient(0deg, black 2px, transparent 8px);
    transform: perspective(100px) rotateX(3deg);
    opacity: 0.2;
}
#bg2 {
    background-image: radial-gradient(circle at 50% 40%, transparent 70%, rgba(0,0,0,0.4));
}
.star {
    position: absolute;
    border-radius: 50%;
}
#uiContainer {
    position: absolute; top: 0; left: 0; width: 100vw; height: 100vh; text-align: center;
}
h1 { 
    color: var(--c1); 
    text-shadow: 5px 5px 5px black, 0 0 0.5em var(--c1);
}
#gameContainer {
    padding: 0 1em;
    border-radius: 0.5em;
    max-width: 50vw;
    min-width: 20em;
    background-image: linear-gradient(var(--c1),var(--c4));
    margin-left: auto;
    margin-right: auto;
    opacity: 0.8;
    box-shadow: 10px 10px 10px black;
    text-align: center;
    display: flex;
    flex-direction: column;
}
table {
    border-collapse: collapse;
    margin: 0 4em;
}
.cell { padding: 1em;  margin: 0; }
#cell2, #cell5, #cell8 {
    border-left: 1px solid white;
    border-right: 1px solid white;
}
#cell4, #cell5, #cell6 {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}
button {
    outline: none;
    border: 1px solid white;
    background-color: var(--c2);
    color: white;
    padding: 0.5em;
}
button~button { margin-left: -1px; }

#planetContainer {
    position: absolute;
    height: 200vw;
    top: 50%;
    animation: spin 60s linear infinite;
}
.planet { 
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow:
      inset 0 0 50px #fff,
      inset 20px 0 80px #f0f,
      inset -20px 0 80px #0ff,
      inset 20px 0 300px #f0f,
      inset -20px 0 300px #0ff,
      0 0 50px #fff,
      -10px 0 80px #f0f,
      10px 0 80px #0ff;
}
@keyframes spin {
    100% { transform: rotate(360deg); }
}

a { text-decoration: none; }
a:visited { color: var(--c0); }
a:hover { color: var(--c1); }
a:active { color: var(--c1); }